html{
    font-size: 10px;
}

.form{
    display: grid;
}

.label{
    font-size: 1.5rem;
    margin-bottom: 0.2rem;
    text-transform: uppercase;
}

.input{
    font-size: 1.6rem;
    padding: 0.5rem 2rem;
    line-height: 2.9rem;
    border-radius: 20px;
    border : 1px solid #ddd;
    margin-bottom: 1 rem;
}

.button {
    background-color: rgba(0, 0, 0, 0.75);
    color : white;
    padding: 1rem 2rem;
    border: 1px solid rgba(0, 0, 0, 0.75));
    border-radius: 20px;
    font-size: 1.4rem;
    cursor: pointer;
    transition: background-color 250ms;

}

.button:hover {
    background-color : rgba(0,0,0,0.85);
}

@media (min-width: 786px){
    .form{
        grid-template-columns: auto 1fr auto;
        grid-gap: 1rem;
        align-items: center;
    }

    .input{
        margin-bottom: 0;
    }
}





